@import url(https://fonts.googleapis.com/css?family=Bodoni%20Moda);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: ff-tisa-sans-web-pro, sans-serif
}

.App {
  text-align: center;
}

html {
  background: url(../../static/media/space.bddbc39b.jpg) center;
  background-size: cover;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

button, .btn {
  cursor: pointer;
  width: 100%;
  border: none;
  border-radius: 15px;
  font-size: 15px;
  background: lightgray;
  padding: 1px 0;
  text-align: center;
  box-sizing: border-box;
}

.site-container {
  width: 80px;
  height: 80px;
  border-radius: 15px;
  background-color: rgba(255, 255, 255, 0.2);
  display: flex;
  flex-direction: column;
  position: relative;
  text-decoration: none;
  color: white;
  align-items: center;
  margin: 10px
}

.site-title {
  font-size: 13px;
  white-space: nowrap;
  overflow-x: hidden;
  overflow-y: hidden;
  padding: 0 5px;
  position: absolute;
  bottom: 2px;
}

.favicon-container {
  width: 45px;
  height: 45px;
  top: -10px;
  bottom: 0;
  margin: auto;
  position: absolute;
  background-color: white;
  border-radius: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.favicon-container img {
  width: 20px;
  height: 20px;
}

.options-button {
  position: absolute;
  top: -5px;
  right: 0;
}

.site-group {
  margin: 0 auto;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
}

.folder-icon {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
  align-items: center;
  overflow: hidden;
}

.folder {
  cursor: auto;
  background-color: #23272E;
  transform: translateY(-25%);
  border-width: 5px;
  border-color: #586478;
  border-style: solid;
  border-radius: 20px;
  z-index: 100;
  font-size: 20px;
  padding: 0;
}

.folder .folder-title {
  width: 100%;
  background: #586478;
  margin-bottom: -1px;
  border-radius: 0 0 10px 10px;
  vertical-align: center;
}

.folder-title p {
  margin: 0;
  position: relative;
  top: 3px
}

.settings-button-container {
  position: absolute;
  top: 0;
  right: 0;
  background-color: transparent;
  width: 50px;
  height: 50px;
}

.settings-button {
  cursor: pointer;
  color: rgba(255, 255, 255, 0.5);
  width: 25px;
  height: 25px;
  float: right;
  margin: 10px;
}

.dialog-container {
  width: 100vw;
  height: 100vh;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  left: 0;
  z-index: 999;
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
}

.dialog {
  background-color: white;
  color: black;
  width: 300px;
  position: absolute;
  display: flex;
  flex-direction: column;
  font-size: 20px;
  justify-content: center;
  border-color: #586478;
  border-style: solid;
  border-radius: 20px;
  z-index: 999;
  box-sizing: border-box;
  text-align: left;
  padding: 15px 30px;
}

.dialog form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: left;
}

.close-config {
  position: absolute;
  top: 0;
  right: 0;
  cursor: pointer;
}

.close-config svg {
  width: 24px;
  height: 24px;
}

.dialog input[type=text] {
  border-radius: 7px;
  border: 1px black inset;
  height: 20px;
}

.dialog-buttons {
  width: 100%;
  margin-top: 15px;
  table-layout: fixed;
  border-collapse: collapse;
}

div.settings-dialog {
  color: black;
  justify-content: flex-start;
  text-align: left;
  padding: 15px 30px;
}

.settings-dialog input[type=range] {
  width: 100%;
}

.settings-dialog label {
  margin: 5px 0
}

h2, h3, h4 {
  margin: 5px 0 0;
  text-transform: uppercase;
}

h2 {
  font-size: 25px;
  margin-bottom: 5px;
}

h4 {
  font-size: 15px;
  margin-bottom: 3px;
}

.settings-dialog .btn {
  padding: 5px;
  border-radius: 20px;
}

.settings-dialog label {
  font-size: 15px;
}

#clock {
  font-family: "Bodoni Moda", sans-serif;
  font-size: 100px;
}

